.is-hidden {
  display: none;
}

body {
  background-color: #def3f3;
}

.cat-info {
  text-align: center;
  background-color: #d8f3fb;
  border: 7px ridge #77cff5;
  border-radius: 15px;
  margin: 5px;
  padding: 40px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 90px;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

